<template>
    <div class="bill">
        <span></span>

        <el-divider></el-divider>

        <el-form :inline="true" :model="searchForm" class="demo-form-inline">
            <el-form-item label="">
                <el-date-picker
                        v-model="searchForm.time"
                        type="month"
                        placeholder=""
                        value-format="yyyy-MM">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="searchForm.category" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option v-for="category in categories" :key="category[0]" :label="category[2]"
                               :value="category[0]"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" plain @click="onSearch" icon="el-icon-search"></el-button>
            </el-form-item>
            <el-form-item>
                <span>{{incomeCount}}</span>
                <span>{{expenseCount}}</span>
                <span v-for="item in monthlyCategoryExpenseArray" :key="item.category">
                    <span v-if="item.expenseCount != 0">{{item.categoryName}}{{item.expenseCount}}</span>
                </span>
            </el-form-item>
            <el-form-item class="add">
                <el-button type="primary" plain @click="onAdd" icon="el-icon-plus"></el-button>
            </el-form-item>
        </el-form>

        <div class="table-container">
            <el-table
                    :data="filteredTableData2"
                    border
                    style="width: 100%"
                    :row-class-name="tableRowClassName">
                <el-table-column
                        type="index"
                        label=""
                        width="50"
                        align="center">
                </el-table-column>
                <el-table-column
                        label=""
                        align="center">
                    <template slot-scope="scope">
                        <span>{{ typeFormat(scope.row[0]) }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        align="center">
                    <template slot-scope="scope">
                        <span>{{ timeFormat(scope.row[1]) }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        align="center">
                    <template slot-scope="scope">
                        <span>{{ categoryFormat(scope.row[2]) }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        label=""
                        align="center">
                    <template slot-scope="scope">
                        <span>{{ amountFormat(scope.row[3]) }}</span>
                    </template>
                </el-table-column>
            </el-table>
        </div>

        <el-dialog title="" :visible.sync="dialogFormVisible" center width="25%"
                   :close-on-click-modal="false" :close-on-press-escape="false">
            <el-form :model="dialogForm" :rules="rules" ref="dialogForm">
                <el-form-item label="" :label-width="formLabelWidth" prop="type">
                    <el-select v-model="dialogForm.type" placeholder="">
                        <el-option v-for="type in typeOptions" :key="type.value" :label="type.label"
                                   :value="type.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="time">
                    <el-date-picker
                            v-model="dialogForm.time"
                            type="date"
                            placeholder=""
                            format="yyyy-MM-dd"
                            value-format="timestamp">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="category">
                    <el-select v-model="dialogForm.category" placeholder="">
                        <el-option v-for="category in categories" :key="category[0]" :label="category[2]"
                                   :value="category[0]"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="amount">
                    <el-input-number v-model="dialogForm.amount" :precision="2" :step="0.01" :min="0"></el-input-number>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="doAdd('dialogForm')"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import Papa from 'papaparse';
    import * as _ from 'lodash';
    import moment from 'moment';

    const TYPES = {INCOME: 1, EXPENSE: 0};

    export default {
        data() {
            return {
                searchForm: {
                    time: '',
                    category: ''
                },
                originTableData: [],
                filteredTableData: [],
                filteredTableData2: [],
                categories: [],
                incomeCount: 0,
                expenseCount: 0,
                dialogFormVisible: false,
                dialogForm: {
                    type: 1,
                    time: '',
                    category: '',
                    amount: 0
                },
                rules: {
                    type: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    time: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    category: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    amount: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                formLabelWidth: '120px',
                typeOptions: [{label: '', value: 1}, {label: '', value: 0}],
                monthlyCategoryExpenseArray: [],
            }
        },
        async mounted() {
            await this.loadCategories();

            await this.loadTable();
        },
        methods: {
            tableRowClassName({row}) {
                //
                if (row[0] == TYPES.INCOME) {
                    return 'income-row';
                } else if (row[0] == TYPES.EXPENSE) {
                    return 'expense-row';
                }
                return '';
            },
            typeFormat(value) {
                //
                if (!value && value != 0) {
                    return ''
                }
                return value == 1 ? '' : (value == 0 ? '' : '')
            },
            timeFormat(value) {
                //
                if (!value) {
                    return ''
                }
                return moment(parseInt(value)).utc().format('YYYY-MM-DD');
            },
            categoryFormat(value) {
                //
                if (!value) {
                    return ''
                }
                let categories = [...this.categories];
                return categories.find(item => {
                    return item[0] == value;
                })[2]
            },
            amountFormat(value) {
                //
                if (!value) {
                    return '0.00'
                }
                return parseFloat(value).toFixed(2) + ''
            },
            countTotal(data, type) {
                //
                if (!data) {
                    return 0;
                }
                if (data.length == 0) {
                    return 0;
                }
                let filteredArr = data.filter(item => {
                    return item[0] == type
                })
                let result = 0
                for (let i = 0; i < filteredArr.length; i++) {
                    result += parseFloat(filteredArr[i][3])
                }
                return parseFloat(result).toFixed(2)
            },
            async loadTable() {
                //
                let billCSV = await this.$http.get('/bill.csv');
                Papa.parse(billCSV, {
                    encoding: "ANSI",
                    complete: res => {
                        // UTF8 \r\n\n
                        let data = res.data;
                        if (data[data.length - 1] == "") {
                            //
                            data.pop();
                        }
                        this.originTableData = [...data];

                        let filteredTableData = [..._.slice(data, 1)];
                        this.filteredTableData = [...filteredTableData]
                        this.filteredTableData2 = [...filteredTableData]

                        this.incomeCount = this.countTotal(this.filteredTableData2, TYPES.INCOME)
                        this.expenseCount = this.countTotal(this.filteredTableData2, TYPES.EXPENSE)
                    }
                });
            },
            async loadCategories() {
                //
                let categoriesCSV = await this.$http.get('/categories.csv');
                Papa.parse(categoriesCSV, {
                    encoding: "ANSI",
                    complete: res => {
                        // UTF8 \r\n\n
                        let data = res.data;
                        if (data[data.length - 1] == "") {
                            //
                            data.pop();
                        }
                        this.categories = [..._.slice(data, 1)];
                    }
                });
            },
            onAdd() {
                //
                this.dialogFormVisible = true;
                this.$nextTick(() => {
                    //DOM
                    this.resetForm('dialogForm');
                    //
                    this.dialogForm.time = new Date().getTime()
                    if (this.categories && this.categories.length > 0) {
                        this.dialogForm.category = this.categories[0][0]
                    } else {
                        this.dialogForm.category = ''
                    }
                })
            },
            doAdd(formName) {
                //
                this.$refs[formName].validate((valid) => {
                    //
                    if (valid) {
                        let dialogForm = this.dialogForm
                        let array = [
                            dialogForm.type.toString(),
                            dialogForm.time,
                            dialogForm.category,
                            dialogForm.amount.toString()
                        ];
                        let filteredTableData = [...this.filteredTableData]
                        //
                        filteredTableData.push(array)

                        //csvjson
                        let exportJSONArray = this.csv2json(filteredTableData);

                        //jsoncsv
                        this.exportCsv(exportJSONArray);

                        this.$message({
                            message: '',
                            type: 'success'
                        });
                    } else {
                        this.$message({
                            message: '',
                            type: 'error'
                        });
                        return false;
                    }
                });
            },
            csv2json(exportArray) {
                //csvjson
                let jsonArray = []
                exportArray.forEach(item => {
                    let obj = {}
                    obj.type = item[0]
                    obj.time = item[1]
                    obj.category = item[2]
                    obj.amount = item[3]
                    jsonArray.push(obj)
                })
                return jsonArray;
            },
            exportCsv(exportList) {
                //jsoncsv
                let csv = Papa.unparse(exportList);
                //Blob createObjectURL
                let content = new Blob([csv]);
                //url
                let urlObject = window.URL || window.webkitURL || window;
                let url = urlObject.createObjectURL(content);
                //<a></a>DOM
                let el = document.createElement("a");
                //
                el.href = url;
                el.download = "bill.csv";
                //
                el.click();
                //
                urlObject.revokeObjectURL(url);
            },
            resetForm(formName) {
                //
                this.$refs[formName].resetFields();
            },
            onSearch() {
                //
                this.filteredTableData2 = this.filteredTableData.filter(item => {
                    let timeFlag = true;
                    let categoryFlag = true;
                    if (this.searchForm.time) {
                        timeFlag = (this.timeFormat(item[1]).substring(0, 7) == this.searchForm.time);
                    }

                    if (this.searchForm.category) {
                        categoryFlag = (item[2] == this.searchForm.category);
                    }

                    return timeFlag && categoryFlag;
                })

                //
                let filteredTableData2 = [...this.filteredTableData2]
                let groupedItems = _(filteredTableData2).groupBy(item => item[2]).map((items, item) => {
                    return {
                        category: item,
                        items: items
                    }
                }).value()

                //
                this.countDetail(groupedItems, TYPES.EXPENSE);

                //
                groupedItems.sort((obj1, obj2) => {
                    let obj1sum = this.sum(obj1)
                    let obj2sum = this.sum(obj2)
                    if (obj1sum < obj2sum) {
                        // , a  b
                        return -1;
                    }
                    if (obj1sum > obj2sum) {
                        return 1;
                    }
                    return 0;
                });

                //
                let convertedItems = this.convertBack(groupedItems)
                this.filteredTableData2 = [...convertedItems]

                //
                this.incomeCount = this.countTotal(this.filteredTableData2, TYPES.INCOME)
                this.expenseCount = this.countTotal(this.filteredTableData2, TYPES.EXPENSE)
            },
            sum(obj) {
                //
                let items = obj.items
                let result = 0
                for (let i = 0; i < items.length; i++) {
                    result += parseFloat(items[i][3])
                }
                return result
            },
            convertBack(groupedItems) {
                //
                let arr = []
                groupedItems.forEach(item => {
                    item.items.forEach(item2 => {
                        arr.push(item2)
                    })
                })
                return arr
            },
            countDetail(data, type) {
                //
                if (!data) {
                    this.monthlyCategoryExpenseArray = []
                    return;
                }
                if (data.length == 0) {
                    this.monthlyCategoryExpenseArray = []
                    return;
                }

                let monthlyCategoryExpenseArray = []
                data.forEach(item => {
                    //
                    item.categoryName = this.categoryFormat(item.category);
                    let items = item.items;
                    let expenseCount = 0
                    for (let i = 0; i < items.length; i++) {
                        if (items[i][0] == type) {
                            expenseCount += parseFloat(items[i][3])
                        }
                    }

                    //
                    item.expenseCount = parseFloat(expenseCount).toFixed(2);

                    monthlyCategoryExpenseArray.push(item)
                })

                this.monthlyCategoryExpenseArray = [...monthlyCategoryExpenseArray]
            },
        },
    }
</script>

<style>
    .el-table .income-row {
        background: rgb(240, 249, 235);
    }

    .el-table .expense-row {
        background: rgb(254, 240, 240);
    }

    .el-dialog .el-form-item > div > div {
        width: calc(100% - 40px);
    }
</style>

<style scoped>
    .bill .el-form {
        position: relative;
    }

    .bill .el-form .add {
        position: absolute;
        right: 0;
    }

    .bill .table-container {
        height: calc(100vh - 150px);
        overflow: auto;
    }
</style>